<template>
  <div class="HelloWorld">
   <h2 ref="flag_1" v-on:click="click_1">{{mgs}}</h2>
   <div class="int">
      <input type="text" placeholder="请输入关键词搜索">
      <img src="../assets/so.png" alt="" class="so_img">
   </div>
   <div class="hello_list">
     <div class="list"></div>
     <div class="list"></div>
     <div class="list"></div>
     <div class="list"></div>
     <div class="list"></div>
     <div class="list"></div>
     <div class="list"></div>
     <div class="list"></div>
   </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      mgs: '测试区',
      red_1:"red1"
    }
  },
  methods: {
     click_1: function(event){
      this.$refs.flag_1.style.color="red";
      this.$refs.flag_1.style.backgroundColor="blue";
      const my=document.getElementById("my")
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.HelloWorld{
  background-color: #fff;
}
h1, h2 {
  font-weight: normal;
  text-align: center;
  background-color: #fff;
  transition: all 6s;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.red1{
  color: red!important;
}
.int{
  width: 80%;
  height: 30px;
  margin: 0 auto;
  outline: none;
  border: 1px solid #EBA56D;
  border-radius: 4px;
  padding: 5px 30px 5px 5px;
  box-sizing: border-box;
  position: relative;
  background-color: #EEEEEE;
  margin-bottom: 12px;
  margin-top: 12px;
}
.int>input{
  width: 100%;
  height: 20px;
  outline: none;
  border: none;
  line-height: 20px;
  background-color: #EEEEEE;
  color: #757575;
}
.so_img{
  height: 26px;
  position: absolute;
  top: 2px;
  right: 4px;
}
.hello_list{
  width: 100%;
  height: auto;
  background-color: #EEEEEE;
  overflow: hidden;
  border-bottom: 1px solid red;
}
.list{
  width: calc(50% - 6px);
  margin-left: 4px;
  margin-top: 4px;
  height: 200px;
  background-color: #fff;
  float: left;
}
</style>



