<template>
  <div id="app">
    <div id="header" v-on:click="test">
      <img onclick="javascript:history.back(-1);" src="./assets/back.png" alt="">
      {{mgs}}
    </div>
    <div id="con_sum_hidden" style="display: none"></div>
    <div id="main_main">
      <transition name="show"> 
        <router-view/>
      </transition>
    </div>
    <div id="foot" >
      <router-link to="/">首页</router-link>
      <router-link to="/HelloWorld">测试区</router-link>
      <router-link to="/shopp">购物车<div class="sum_flag_my">1</div></router-link>
      <router-link to="/my"id="my">我的</router-link>
    </div>
  </div>
</template>
<script>
export default {
  name: 'App',
  data (){
    return {
      mgs: "单页应用程序",
      transitionName: 'slide-left'  
    }
  },
  methods: {
    test:function(event){
      console.log(event.target)
    }
  },  
}
</script>
<style>
*{
  padding: 0;
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
#header{
  height: 40px;
  width: 100%;
  line-height: 40px;
  text-align: center;
  background-color: #eee;
}
#header>img{
  float: left;
  height: 24px;
  margin-top: 8px;
  /*vertical-align: middle;*/
}
#main_main{
  width: 100%;
  margin-bottom: 40px;
  min-height: 400px;
  overflow: hidden;
}
 
#foot{
  height: 40px;
  width: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: #eee;
}
#foot>a{
  position: relative;
  display: inline-block;
  float: left;
  width: 25%;
  height: 40px;
  text-align: center;
  line-height: 40px;
  color: black;
  text-decoration: none;
}
.sum_flag_my{
  position: absolute;
  top: 2px;
  right: 0px;
  width: 20px;
  height: 20px;
  border-radius: 10px 0 10px 10px;
  background-color: #EBA56D;
  color:#fff;
  line-height: 20px;
  text-align: center;
  overflow: hidden;
} 
.router-link-exact-active{
  color: red!important;
} 
.show-enter-active,.show-leave-active{
         transition:all 0.4s;
     }
     .show-enter,.show-leave-to{
         opacity: 0; 
     }
     .show-enter-to,.show-leave{
         transition:opacity 1s 0.4s;/*延迟0.4s,等待隐藏动画完成*/
         opacity:1; 
     }
</style>



